input {
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    width: 50%;
    box-sizing: border-box;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    outline: none;
    transition: all 0.3s ease;
}
button {
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    width: 50px;
    box-sizing: border-box;
}