.App {
    text-align: center;
  }

  ul {
    list-style: none;
    padding: 0;
    text-align: left;
  }
  .btn {
    border-style: initial;
    /* border-width: 0; */
    box-shadow: rgba(25, 25, 25, .04) 0 0 1px 0, rgba(0, 0, 0, .2) 0 3px 4px 0;
    font-family: Arial, sans-serif;
    font-size: 1em;
    padding: 0 25px;
    margin: 15px 15px;
    height: 100px;
    width: 100px;
    border-radius: 50%;
  }

  .btn:hover {
    background-color: #afe6c3;
    transform: scale(1.15);
    cursor: pointer;
    transition: all 200ms;
  }
