
.left-align {
    text-align: left;
}

.recipe-heading {
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 0.5rem;
    background-repeat: no-repeat;
}

/* This CSS code defines the styling for a container that displays a recipe.
The container uses flexbox to align its child elements horizontally. */

.recipe-container {
  display: flex;
  /* Set the container to use flexbox */
  align-items: center;
  /* Vertically align child elements in the center */
}

/* This CSS code applies to the first child element inside the recipe container.
It aligns the child element and its contents horizontally using flexbox. */

.recipe-container>div:first-child {
  display: flex;
  /* Set the first child element to use flexbox */
  align-items: center;
  /* Vertically align the child element's contents in the center */
}

/* This CSS code applies to any images inside the recipe container.
It adds a margin to the right of the image to create space between it and other elements. */

.recipe-container img {
  margin-right: 20px;
  /* Add 20 pixels of margin to the right of the image */
}


